import { useState, useEffect } from 'react';
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Routes, Route} from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import { Container, Row, Col } from 'react-bootstrap';
import Paging2 from './paging2';
import Detail from './detail';
import axios from 'axios'
import Form from 'react-bootstrap/Form';


function Incident() {
 // 데이터 호출 시작. DB 서버 데이터 호출 대신 사용
 let [data, setData] = useState([]);
 useEffect(()=>{
  (async () =>{
  axios.get('https://violetleaf44.github.io/react/data/indata.json').then((response)=>{
     setData(response.data);
      })
      .catch(()=>{
        console.log('failed')
      });
    }
  )();
},[])
 // 데이터 호출 끝

 const [search, setSearch] = useState("");
 const [search2, setSearch2] = useState("");
 const numChange = (e) => {
   setSearch(e.target.value);
   console.log(e.target.value);
 }

 const nameChange = (e) => {
   console.log(e.target.value);
 }
 const titleChange = (e) => {
   console.log(e.target.value);
 }
 const OPTIONS = [
   { value: "", name: "-------------------------" },
   { value: "서비스요청", name: "서비스요청" },
   { value: "장애요청", name: "장애요청" },
   { value: "변경요청", name: "변경요청" },
 ];

 const SelectBox = (props) => {
   const typeChange = (e) => {
     setSearch2(e.target.value);
     console.log(e.target.value);
   };

   return (
     <select onChange={typeChange}>
       {props.options.map((option) => (
         <option
           key={option.value}
           value={option.value}
           defaultValue={props.defaultValue === option.value}
         >
           {option.name}
         </option>
       ))}
     </select>
   );
 };


 return (
   <div>
     <Container className='sr_table'>
       {/* 검색 시작*/}
       <Row>
         <Col className='th_ser'><span className='ser_bar'>|</span> 검색</Col>
       </Row>
       <Row className='sr_text'>
         <Col md="1">요청번호</Col>
         <Col md="3">
           <Form.Group className="mb-3">
             <Form.Control placeholder="Disabled input" disabled />
           </Form.Group>
         </Col>
         <Col md="1">상세유형</Col>
         <Col md="3">
          <Form.Group className="mb-3">
            <Form.Select id="srType" >
              <option value="네트워크">네트워크</option>
              <option value="서버">서버</option>
              <option value="OA">OA</option>
              <option value="서비스">서비스</option>
              <option value="어플리케이션">어플리케이션</option>
              <option value="그룹웨어">그룹웨어</option>
              <option value="">-- 초기화 --</option>
            </Form.Select>
           </Form.Group>
           </Col>
         <Col md="1">요청자</Col>
         <Col md="3">
         <Form.Group className="mb-3">
             <Form.Control placeholder="Disabled input" disabled />
           </Form.Group>
          
         </Col>
       </Row>
       <Row className='sr_text'>
         <Col md="1">요청제목</Col>
         <Col md="11">
           <Form.Group className="mb-3">
             <Form.Control placeholder="Disabled input" disabled />
           </Form.Group>
         </Col>
       </Row>
       <Row className='sr_text'>
         <Col md="1">요청상태</Col>
         <Col md="3">
          <Form.Group className="mb-3">
           <Form.Select id="srState">
             <option value="요청접수">요청접수</option>
             <option value="문제이관">문제이관</option>
             <option value="이관접수">이관접수</option>
             <option value="분류대기">분류대기</option>
             <option value="배포대기">배포대기</option>
             <option value="처리완료">처리완료</option>
             <option value="">-- 초기화 --</option>
           </Form.Select>
           </Form.Group>
          </Col>
         <Col md="1">요청일</Col>
         <Col md="3">
         <Form.Group className="mb-3">
           <Form.Control placeholder="Disabled input" disabled />
         </Form.Group>
         </Col>
         <Col md="1">완료일</Col>
         <Col md="3">
         <Form.Group className="mb-3">
           <Form.Control placeholder="Disabled input" disabled />
         </Form.Group>
         </Col>
         <Col md="4"></Col>
       </Row>
       <Row>
         <Col md="4"></Col>
         <Col md="4" className='btn_inbox'><Button variant="primary">검색</Button></Col>
         <Col md="4"></Col>
       </Row>
       <Row className='row_line'>
       <Col className='th_ser'><span className='ser_bar'>|</span> 검색결과</Col>
       </Row>
     </Container>
     {/* 검색 끝 */}

     {/* 라우트 시작 */}
     <Routes>
       <Route path="/" element={<Paging2 data={data} />} />;
       <Route path="/detail/:id" element={<Detail data={data} />} />;
       <Route path="*" element={<div>404 없는 페이지입니다.</div>} />
     </Routes>
     {/* 라우트 끝 */}

   </div>
 );
}

